<template>
  <el-row class="warp" style="padding:20px 0 0 20px;">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main">
     <el-form :inline="true">
      <el-form-item>
        <el-button type="primary" size="medium" @click="newone"></el-button>
      </el-form-item>
      <el-form-item>
        <el-input v-model="filter.name" placeholder="///" style="min-width: 260px;" ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="medium" @click="newone"></el-button>
        <el-button size="medium" @click="newone"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="list" v-loading="loading" border stripe size="small">
      <el-table-column prop="name" label="" min-width="80" align="center">
      </el-table-column>
      <el-table-column prop="url" label="" min-width="200" align="center">
        <template slot-scope="scope">
          <img :src="scope.row.url" style="max-width:100px;max-height:100px;" />
        </template>
      </el-table-column>
      <el-table-column prop="name" label="" min-width="200" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="200" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="150" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="200" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="150" align="center">
      </el-table-column>
      <el-table-column prop="name" label="/" min-width="150" align="center">
      </el-table-column>
      <el-table-column prop="name" label="" min-width="150" align="center">
      </el-table-column>
      <el-table-column label="" min-width="200" align="center">
       <template slot-scope="scope">
        <el-tooltip class="icon" effect="dark" content="" placement="top" @click="handleEdit">
          <img src="../../../static/images/icon/edit.png">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="bottom" @click="handleEdit">
          <img src="../../../static/images/icon/look.png">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="top" @click="handleEdit">
          <img src="../../../static/images/icon/ewcode.png">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="bottom" @click="handleEdit">
          <img src="../../../static/images/icon/check.png">
        </el-tooltip>
        <el-tooltip class="icon" effect="dark" content="" placement="top" @click="handleDelete">
          <img src="../../../static/images/icon/delete.png">
        </el-tooltip>
      </template>
    </el-table-column>
  </el-table>

  <el-pagination style="float:left;margin-top:20px;" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="limit" @current-change="handleCurrentChange" @size-change="handleSizeChange" layout="total,sizes, prev, pager, next, jumper" :total="count" prev-text="" next-text="">
  </el-pagination>
</el-col>

<el-col>
  <el-dialog :title="diatitle" :visible.sync="dialogNewVisible" width="500" center style="min-width: 500px">
    <el-form ref="newadv" :model="newadv" label-width="120px" :rules="rules">

      <el-form-item label="">
        <el-upload class="upload-demo" :action="upUrl" :before-upload="beforeUpload" :on-success="handleSuccess" :show-file-list="false" accept="image/*">
          <img :src="imgSrc" class="pre-img" style="width:50%;height:25%;border:2px dashed #ccc;border-radius:10px;display: block" >
          <el-button size="small" type="primary" style="display: block;margin-top: 20px;"></el-button>
          <div slot="tip" class="el-upload__tip">JPG/PNG1M</div>
        </el-upload>
      </el-form-item>

<!--       <el-form-item label=":">
        <el-select v-model="newadv.positionid" placeholder="">
          <el-option v-for="item in postarr" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item> -->

      <el-form-item label=":">
        <el-input v-model="newadv.link" placeholder=""></el-input>
      </el-form-item>


      <el-form-item label="">
        <el-upload :action="upUrl" list-type="picture-card" :on-remove="handleRemove" :on-success="handleUploadLun" :on-exceed="handleExceed" :file-list="album" :limit="3">
          <i class="el-icon-plus"></i>
        </el-upload>
<!--       <el-dialog :visible.sync="dialogVisible1">
        <img width="100%" :src="dialogImageUrl1">
      </el-dialog> -->
    </el-form-item>



    <el-form-item style="margin-left: calc(50% - 200px);">
      <el-button type="primary" @click="save()"> </el-button>
      <el-button @click="dialogNewVisible = false"> </el-button>
    </el-form-item>
  </el-form>
</el-dialog>
</el-col>


<el-col>
  <el-dialog title="" :visible.sync="dialogDelVisible" width="30%">
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" @click="submitdel()"> </el-button>
      <el-button @click="dialogDelVisible = false"> </el-button>
    </div>
  </el-dialog>
</el-col>

</el-row>
</template>



<script>


  import baseUrl from '../../api/api';

  export default {
    data() {
      return {
        currentPage: 1,
        list:[{
          name:'11',
          url:'../static/images/gold.jpg'
        }],
        loading: false,
        count:100,
        limit:10,
        dialogNewVisible:false,
        dialogDelVisible:false,
        filter:{
          name:''
        },
        putorup:'up',
        showUrl:baseUrl,
        upUrl:baseUrl+'upload',
        imgSrc:"../static/images/default.png",
        newadv:{
          link:'',
          intro:'',
          positionid:'',
          album:''
        },
        diatitle:'',
        postarr:[],
        rules: {
          link: [{required: true, trigger: 'blur',message: ''}]
        },
        editId:'',
        delId:'',
        album:[]
      };
    },

    methods:{
      getlist(){

      },

      newone(){
       this.putorup='up';
       this.imgSrc="../static/images/default.png";
       this.diatitle='',
       this.dialogNewVisible=true
     },

     beforeUpload(file) {
      const isLt1M = file.size / 1024 / 1024 < 1;
      if (!isLt1M) {
        this.$message.error(' 1MB!');
      }
      return isLt1M;
    },

    handleSuccess(res, file) {
      this.imgSrc = URL.createObjectURL(file.raw);
      this.upimgurl = res.data.url;
    },

    save(){
      this.dialogNewVisible=false
    },

    handleEdit(index, row){
      this.diatitle='';
      this.dialogNewVisible = true;
      this.putorup='put';
      this.editId = row.id;
      this.imgSrc=this.showUrl+row.url;
    },

    handleDelete(index, row) {
      this.dialogDelVisible = true;
      this.delId = row.id;
    },

    submitdel(){
      this.dialogDelVisible = false;
    },

    handleCurrentChange(val) {
      this.currentPage = val;
      this.getlist();
    },


    handleSizeChange(val){
      this.limit = val;
      this.getlist();
    },

    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handleExceed(files, fileList) {

      this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
    },

    handleUploadLun(res, file){

      this.newadv.album += res.sucesss + ",";
    },
  },

  mounted: function () {
    this.getlist();
  }
}
</script>


<style>
.icon{
  width: 20px;
  height: 20px;
  margin: 2px;
}
</style>



